import React from 'react';


const TableHead = () => {
    return (
        <thead>
            <tr>
                <th>Nombre</th>
                <th>Oficio</th>
                <th>Borrar</th>
            </tr>
        </thead>
    );
};
const TableBody = () => {
    return (
        <tbody>
            <tr>
                <td>Luis</td>
                <td>Programador</td>
            </tr>
            <tr>
                <td>Noelia</td>
                <td>Profesora</td>
            </tr>
            <tr>
                <td>Ada</td>
                <td>Cientifica</td>
            </tr>
        </tbody>
    );
};

//const Table = () => {
  //  return (
    //    <table>
      ///      <TableHead/>
         //   <TableBody/>
        //</table>
    //);
//};
function Table  () {
    return (
        <table>
            <TableHead/>
            <TableBody/>
        </table>
    );
};


export default Table;